*{
margin:0;
padding: 0;
box-sizing: border-box;
}

body{
    font-family: 'montseratt', sans-serif;

}

.app{
    background-image: url('sunny.jpg');
    background-size: cover;
    background-position: bottom;
    transition: 0.4s ease-out;
}

.app1{
    background-image: url('app1.jpg');
    background-size: cover;
    background-position: bottom;
    transition: 0.4s ease-out;
}


main{
    min-height: 100vh;
    padding: 25px;
}

.search-box{
    
    width: 100%;
    margin: 0 0 75px;
    
}

.search-box .search-bar{
    display: block;
    width: 50%;
    margin-left: 25%;
    padding: 15px;

    outline: none;
    border: none;
    appearance: none;
    

    background-color: rgba(255, 255, 255, 0,5);
   margin-top: -25px;
   border-radius: 8px;
   box-shadow: 0px 5px rgba(0,0,0,0.2);
   color:#313131;
   font-size: 20px;
   transition: 0.4s ease;
   
}

.search-box .search-bar:focus{
    background-color: rgba(255,255,255, 0.75);
}

.location-box .location{
    color:#fff;
    font-size: 32px;
    font-weight: 500;
    text-align: center;
    text-shadow: 3px 3px rgba(50,50,70,0.5);
}

.location-box .date{
    color:#fff;
    font-size: 20px;
    font-weight: 300;
    font-style: italic;
    text-align: center;
    text-shadow: 2px 2px rgba(50,50,70,0.5);
}

.weather-box{
    text-align: center;
}

.weather-box .temp{
position: relative;
display: inline-block;
margin:30px auto;
background-color: rgba(34, 18, 18, 0.849);
border-radius: 16px;
color: #fff;

padding: 15px 25px;
font-size: 102px;
font-weight: 900;
text-shadow: 3px 6px rgba(50,50,70, 0.5);
text-align: center;
box-shadow: 3px 6px rgba(0,0,0,0.2);
}

.weather-box .weather{
    color: #fff;
    font-size: 48px;
    font-weight: 700;
    text-shadow: 3px 3px rgba(50,50,70,0.5);
}